.paper {
    position: absolute;
    width: 413px;
    height: 600px;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);

    background: #F9F9FE;
    border-radius: 8px;

    display: flex;
    justify-content: center;
    text-align: center;
}

.formControl {
    width: 350px;
}

.loader {
    height: 20px;
}

.description {
    font-family: "SF UI Display", sans-serif;
    font-style: normal;
    font-weight: 600;
    font-size: 14px;
    line-height: 24px;

    color: #2D2E46;
    padding: 60px 0;

    opacity: 0.5;
}