.aboutMe {
    min-width: 250px;
    background: #ECECF9;
    border-bottom-left-radius: 8px;
    border-top-left-radius: 8px;
}

.personal {
    width: 250px;
    height: 220px;
    background: #D9D9F1;
    border-bottom-left-radius: 8px;
    border-bottom-right-radius: 8px;
    border-top-left-radius: 8px;

    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

.avatar {
    width: 100px;
    height: 100px;
    border-radius: 50px;
    margin: 5px;
    background-color: #ECECF9;
}

.name {
    font-family: "Poppins", sans-serif;
    font-style: normal;
    font-weight: 600;
    font-size: 18px;
    line-height: 27px;

    color: #2D2E46;
    padding: 8px 0;
}

.description {
    font-family: "SF UI Display", sans-serif;
    font-style: normal;
    font-weight: normal;
    font-size: 14px;
    line-height: 17px;

    color: #2D2E46;
    padding: 0 0 8px 0;

    opacity: 0.5;
}

.packsTable {
    display: flex;
    flex-direction: column;
    width: 100%;
    padding: 30px 50px;
}