.header {
    font-family: "Poppins", sans-serif;
    font-style: normal;
    font-weight: 600;
    font-size: 22px;
    line-height: 33px;

    color: #2D2E46;
    padding-bottom: 15px;
}

table {
    width: 100%;
    border-collapse: collapse;
    box-shadow: 0 14px 28px rgba(0,0,0,0.05), 0 10px 10px rgba(0,0,0,0.05);
    text-align: center;
}

table thead tr {
    font-weight: bold;
    border-top: 1px solid #e8e9eb;
    background-color: #ECECF9;;
}

table thead tr td {
    font-family: "SF UI Display", sans-serif;
    font-style: normal;
    font-weight: bold;
    font-size: 14px;
    line-height: 16px;

    color: #2D2E46;
    padding: 16px 24px;
}

table tbody tr td {
    font-family: "SF UI Display", sans-serif;
    font-style: normal;
    font-weight: normal;
    font-size: 14px;
    line-height: 16px;

    color: #000000;
    padding: 7px 10px;
}

table tbody tr:nth-child(odd) {
    background-color: #FFFFFF;
}

table tbody tr:nth-child(even) {
    background-color: #F8F7FD;
}
